.video-responsive {
    overflow: hidden;
    padding-bottom: 56.25%;
    position: relative;
    /* height: 100%; */
    width: 100%;
    margin: 0 auto;
  }
  
  .video-responsive iframe {
    left: 0;
    top: 0;
    width: 100%;
    height: -webkit-fill-available;
    position: absolute;
  }
